@font-face {
    font-family: 'roadnumbersregular';
    src: url(/region-codes/static/media/roadnumbers.84d8e48f.woff2) format('woff2'),
         url(/region-codes/static/media/roadnumbers.488f872a.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}
:root, body[scheme="bright_light"] {
  --background_page: #f5f6fa;
  --text_primary: #233756;
  --accent: #4454ff;
  --header_background: #8b44f7;
  --header_text: #ffffff;
  --tabbar_active_icon: #4454ff;
  --counter_primary_background: #4454ff; }

.Tabbar {
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.Header {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.PanelHeader__left {
  color: #ffffff; }

.PanelHeader__content {
  color: #ffffff; }

.snackbarIcon {
  background-color: var(--destructive);
  border-radius: 100%; }

.noteText {
  color: #000000;
  color: var(--text-primary); }
  .noteText img {
    max-width: 100%; }
  .noteText__image {
    width: 200px;
    margin: 0 auto; }

.cellImage {
  width: 200px; }

.openFinesAppIcon {
  width: 24px;
  border-radius: 4px;
  margin-left: 16px; }

.HeaderButton--android .openFinesAppIcon {
  margin-top: 12px;
  margin-left: 0px; }

.roadNumber {
  background-color: #ffffff;
  border-radius: 10px;
  border: 2px solid #000000;
  box-shadow: 4px 3px 10px -8px rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  font-size: 35px;
  font-family: 'roadnumbersregular';
  width: 220px;
  margin: 0 auto; }
  .roadNumber__regNumber {
    padding: 8px 0;
    display: inline-block; }
  .roadNumber .left {
    padding-left: 20px;
    padding-right: 10px;
    border-right: 1px solid #000000;
    width: 60%; }
  .roadNumber .right {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25%; }

a {
  color: var(--text_primary); }

.sanctions {
  display: inline-block; }
  .sanctions::first-letter {
    text-transform: uppercase; }

